import {
  Component, HostListener, OnDestroy, OnInit
} from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import { UserLoginAttempt } from '@biz-nest/auth-client';
import { FrontendLogger } from '@biz-nest/core-client';
import {
  AppNameService,
  EventbusService,
  ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop,
  TooltipService, WindowService
} from '@front-shared-lib';
import { EagleInfoClientError } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminDateFilterFieldComponent } from '../../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalComponent } from '../../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../../misc/admin-top-bar/admin-top-bar.component';
import { ViewUserLoginAttemptsComponent } from '../view-user-login-attempts/view-user-login-attempts.component';

@Component({
  selector: 'app-user-login-attempts-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent,
    ViewUserLoginAttemptsComponent
  ],
  templateUrl: './user-login-attempts-list.component.html'
})
export class UserLoginAttemptsListComponent
implements OnInit, OnDestroy {
  id: string;
  cleanup?: () => void;
  catFilter?: string;
  listCompose = new ListComposeAdminPanel<UserLoginAttempt>(
    UserLoginAttempt,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  showViewModal = false;
  viewUserLoginAttempt?: UserLoginAttempt;

  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (userLoginAttempt: UserLoginAttempt) => {
        this.showViewModal = true;
        this.viewUserLoginAttempt = userLoginAttempt;
      }
    },
    {
      label: 'View User',
      icon: 'visibility',
      event: (userLoginAttempt: UserLoginAttempt) => {
        this.viewUser(userLoginAttempt.user._id);
      }
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (userLoginAttempt: UserLoginAttempt) => {
        this.listCompose.openDeleteOneModal(
          userLoginAttempt._id,
          'Delete Login History',
          'Are You Sure You Want To Delete This Login History Record?'
        );
      }
    }
  ];

  constructor(
    public tooltipService: TooltipService,
    public eventbus: EventbusService,
    private router: Router,
    private windowService: WindowService,
    private toastr: ToastrService,
    private route: ActivatedRoute,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {
    this.id = this.route.snapshot?.params['id'];
  }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    if (this.id !== 'all') {
      this.listCompose.filterProps = {
        propFilter: {
          user: this.id
        }
      };
    }

    this.listCompose.getDataArr();
    this.mainBarTitle.title = 'User Login History - Manage User Login History Records';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Easily manage and view user login attempts for
      your organization to ensure effective security monitoring.`
    });

    scrollToTop(this.windowService);
  }

  viewUser(id: string) {
    FrontendLogger.debug('viewUser', id);
    const found = this.listCompose.dataArr.find(val => val.user.urId === id);

    if (!found) {
      throw new EagleInfoClientError('no user');
    }
    this.router.navigate(['/manage-user/' + found._id]);
  }


  ngOnDestroy() {
    this.cleanup?.();
    this.listCompose.cleanup?.();
  }
}

