import {
  Component, EventEmitter, Input,
  Output
} from '@angular/core';
import { Router, RouterModule } from '@angular/router';
import {
  FrontendLogger,
  getBizNestErrorResponseMessage,
  UserLoginAttempt
} from '@biz-nest/auth-client';
import {
  EventbusService, TooltipService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-view-user-login-attempts',
  standalone: true,
  imports: [
    RouterModule
  ],
  templateUrl: './view-user-login-attempts.component.html'
})
export class ViewUserLoginAttemptsComponent {
  @Input({ required: true }) userLoginAttempt!: UserLoginAttempt;
  @Input({ required: true }) showModal!: boolean;
  @Output() closeModal = new EventEmitter<void>();

  constructor(
    public eventbus: EventbusService,
    public tooltipService: TooltipService,
    public toastr: ToastrService,
    private router: Router
  ) {}

  async deleteUserLoginAttempt() {
    try {
      await this.userLoginAttempt.remove();
      this.closeModal.emit();
    } catch (error) {
      FrontendLogger.error(error);
      this.toastr.error(getBizNestErrorResponseMessage(error), 'Error');
    }
  }

  viewUser() {
    this.closeModal.emit();
    this.router.navigate(['/manage-user/' + this.userLoginAttempt.user._id]);
  }
}

